import React from 'react';
import Hero from './Hero'; // Assure-toi d'importer le composant Hero correctement
import './Adherer.css'; // Importe le fichier CSS pour le composant Adherer
import FicheAdhesionPDF from '../assets/fiche_adhesion.pdf';
import adhererImg from '../img/img-adherer.png'; // Chemin vers ton image
import heroImage from '../img/drapeau_italie.jpg';

const Adherer = () => {
    const title = "Adhrer  notre association";
    const description = "Bienvenue sur notre page d'adhsion. Dcouvrez les avantages de devenir membre de notre association.";
    const buttonText = "Commencer";

    const handleScrollToContent = () => {}
        // Fonction pour faire dfiler la page vers la partie suivante

    const handleScrollToSection = (event, sectionId) => {
        event.preventDefault();
        const section = document.getElementById(sectionId);
        if (section) {
            section.scrollIntoView({ behavior: 'smooth' });
        }
    };

    return (
        <div className="adherer-container">
            <Hero
                title={title}
                description={description}
                buttonText={buttonText}
                heroImage= {heroImage}
                scrollTo={handleScrollToContent}
            />

            <div className="adherer-content1">
                
                <img src={adhererImg} alt="Adherer Image" className="adherer-image" />

                <div className="txt-content">
                    <h2 className='h2'>Pourquoi adhrer ?</h2>
                    <h3 className='h3'>Adhrer  l'A.P.I.R.P. vous permet de :</h3>
                    <ol className='custom-ol ol'>
                            <li >Participer activement aux assembles gnrales.</li>
                            <li>Recevoir chaque anne le bulletin imprim de lassociation et des plaquettes gratuites pour la promotion de litalien.</li>
                            <li>
                                Soutenir des initiatives visant  promouvoir lenseignement de litalien en le-de-France, telles que :
                                <ul className='ul'>
                                    <li>- La ralisation de ce site, conu comme un outil de partage et de renseignement pour tous les professeurs ditalien de lle-de-France ;</li>
                                    <li>- La cration et limpression de plaquettes pour la promotion de litalien ;</li>
                                    <li>- Notre participation  des manifestations et des vnements culturels tels que La festa del libro e della cultura italiana ou le Forum des associations franco-italiennes, etc.</li>
                                </ul>
                            </li>
                            <li>Connatre le rseau des professeurs ditalien de la Rgion Parisienne.</li>
                            <li>Pouvoir tre reprsent et soutenu, en toutes circonstances et auprs des instances officielles, par une association reconnue.</li>
                            <li>Bnficier de l'union qui fait la force ; l'A.P.I.R.P. est un moyen de ne pas rester isol et de favoriser des changes actifs sur notre mtier.</li>
                    </ol>
                </div>
            </div>

            <div className="adherer-content2">
                <h2 className='h2'>Qui peut adhrer  lassociation ?</h2>
                <p className='p'>Peuvent faire partie de lAssociation tous les professeurs ditalien de la rgion parisienne en activit ou  la retraite, quel que soit lordre denseignement auquel ils appartiennent.</p>
                <h3>Comment y adhrer ?</h3>
                <p className='p'>Pour devenir membres il faut : remplir une fiche dinscription ET verser annuellement une cotisation, dont le montant est fix chaque anne par le Conseil dadministration.</p>

                <button className="button" onClick={() => window.open(FicheAdhesionPDF, '_blank')}>Tlcharger la fiche d'adhsion</button>
            </div>
            
        </div>
    );
}

export default Adherer;