import React from "react";
import styled from "styled-components";
// Components
import ProjectBox from "../Elements/ProjectBox";
// Assets
import ProjectImg1 from "../../assets/img/projects/bulletin1.png";
import ProjectImg2 from "../../assets/img/projects/bulletin2.png";
import ProjectImg3 from "../../assets/img/projects/bulletin3.png";
import ProjectImg4 from "../../assets/img/projects/bulletin4.png";
import ProjectImg5 from "../../assets/img/projects/bulletin5.png";
import ProjectImg6 from "../../assets/img/projects/bulletin6.png";
import HeaderImage from "../../assets/img/Contact/Header-Contact.jpg";

export function Contact() {
  return (
    <HeaderWrapper className="container flexSpaceCenter">
      <LeftSide className="flexCenter">
        <div>
        <h1 className="extraBold font60">Nous
          <span className="darkColor"> contacter</span>
        </h1>
          <HeaderP className="font15">
          Pour nous signaler tout changement de coordonnes personnelles (adresse de domicile, tlphone, courriel) ou professionnelles (changement dtablissement de rattachement, de statut, etc.), merci denvoyer un courriel  : <span className="semiBold">apirp.association@gmail.com.</span>
          <br />
          Pour les renouvellement dadhsion et pour les commandes de plaquettes pour la promotion de litalien, nos adhrents peuvent aussi se servir des formulaires publis dans les deux dernires page de notre bulletin annuel.</HeaderP>
          <h3>Mail : apirp.association@gmail.com</h3><br />
          <h3>Suivez nous sur <a  href="https://m.facebook.com/APIRP/" target="_blank" className="darkColor">Facebook</a> !</h3>
        </div>
      </LeftSide>
      <RightSide>
        <ImageWrapper>
          <Img className="radius8" src={HeaderImage} alt="office" style={{zIndex: 9}} />
        </ImageWrapper>
       
      </RightSide>
    </HeaderWrapper>
  );
}
const HeaderWrapper = styled.section`
  padding-top: 80px;
  width: 100%;
  min-height: 840px;
  @media (max-width: 960px) {
    flex-direction: column;
  }
`;
const LeftSide = styled.div`
  width: 50%;
  height: 100%;
  @media (max-width: 960px) {
    width: 100%;
    order: 2;
    margin: 50px 0;
    text-align: center;
  }
  @media (max-width: 560px) {
    margin: 80px 0 50px 0;
  }
`;
const RightSide = styled.div`
  width: 50%;
  height: 100%;
  @media (max-width: 960px) {
    width: 100%;
    order: 1;
    margin-top: 30px;
  }
`;
const HeaderP = styled.div`
  max-width: 470px;
  padding: 15px 0 50px 0;
  line-height: 1.5rem;
  @media (max-width: 960px) {
    padding: 15px 0 50px 0;
    text-align: center;
    max-width: 100%;
  }
`;

const GreyDiv = styled.div`
  width: 30%;
  height: 700px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
  @media (max-width: 960px) {
    display: none;
  }
`;
const ImageWrapper = styled.div`
  display: flex;
  justify-content: flex-end;
  position: relative;
  z-index: 9;
  @media (max-width: 960px) {
    width: 100%;
    justify-content: center;
  }
`;
const Img = styled.img`
  @media (max-width: 560px) {
    width: 80%;
    height: auto;
  }
`;

export function Assoc() {
  return (
    <Wrapper id="projects">
      <div className="whiteBg">
        <div className="container">
        <HeaderInfo>
            <h1 className="font40 extraBold darkColor">L'association et ses statuts</h1><br />
            <h1 className="font30 semiBold">Prsentation de l'APIRP: Association des Professeurs d'Italien de la Rgion Parisienn</h1><br />
            <p className="font15"> <br />
            Le 9 mai 1977 tait dclare  la Prfecture de Police de Paris la cration de  L'Amicale des professeurs d'italien de la rgion parisienne . Rebaptise  Association des professeurs d'italien de la rgion parisienne  (APIRP) en 1984, elle eut pour prsident(e)s successifs(ives) des professeurs de Collge et de Lyce de lducation Nationale, Jean Constantin, Pierre Mthivier, Anne Mazire, Gabrielle Kerleroux, Ketty Zanforlini et depuis janvier 2017, Romina De Lucia. 
            </p><p className="font15"> <br />
            Quarante ans aprs  sa cration, sa vocation originelle ( tablir et dvelopper des liens de solidarit et d'amiti entre les italianistes de la rgion parisienne ) reste toujours d'actualit. Mais l'APIRP a volu avec le temps. De nouvelles actions sont en effet apparues ncessaires : tout d'abord, la collecte d'informations fiables au sujet des implantations de postes dans les trois acadmies; ensuite, la mise en commun d'informations culturelles, de ressources pdagogiques, d'ides pour les cours; et enfin, l'entre-aide en cas de mutation, de fragilisation de poste, etc. Pour ce faire, l'APIRP entretient un dialogue rgulier et constructif avec l'Inspection d'italien, en toute indpendance.
            </p><p className="font15"> <br />
            L'APIRP a labor une plaquette de promotion de la langue italienne  destination des parents et des lves en situation de choix, publie un bulletin par an, runit ses adhrents en assemble gnrale au dbut et  la fin de chaque anne scolaire et a galement cr un site Internet et une page Facebook. 
            </p><p className="font15"> <br />
            Elle est prsente lors de manifestations culturelles italo-franaises (Forum des association italiennes du XIII arrondissement, Festa del libro des Blancs Manteaux, rceptions au Centre culturel et  l'Ambassade d'Italie, entre autres). Elle reprsente aussi tous les professeurs d'italien de l'Ile de France au Congrs annuel de la FNAI (Fdration Nationale des Associations d'Italianistes).
            </p><p className="font15 semiBold"> <br />
            Pour nous contacter : apirp.association@gmail.com</p>
          </HeaderInfo>
        </div>
      </div>
      
      
    </Wrapper>
  );
}

const Wrapper = styled.section`
  width: 100%;
  margin: 50px 0;
`;
const HeaderInfo = styled.div`
  @media (max-width: 860px) {
   
  }
`;




