import React from 'react';
import Hero from './Hero';
import UniversityTable from './UniversityTable';
import './Apprendre.css';
import heroImage from '../img/italie_rome.jpg';


const Apprendre = () => {
    const title = "Apprendre l'italien";
    const description = "Bienvenue sur notre page apprendre. Dcouvrez les faons d'apprendre la langue italienne en formation Post-BAC.";
    const buttonText = "Commencer";

    // Dfinition des donnes pour chaque universit (c'est un exemple, tu devras remplacer ces donnes par les tiennes)
    const universitiesDataParis = [
        {
            name: "Universit Paris 3 Sorbonne Nouvelle",
            logo: require('../img/logo_UNI_SorbonneNouvelle.png'),
            website: "https://www.exemple1.com",
            courses: 
            <ul>
              <li>Licence LLCER d'italien</li>
              <li>Licence Sciences du Langage - mineure Langues et civilisations europennes  (italien) </li>
              <li>Licence d'anglais - mineure italien </li>
              <li>Master tudes Italiennes (recherche)</li>
              <li>Master MEEF (Mtiers de l'enseignement, de l'ducation et de la formation) parcours italien</li>
              <li>Master pro changes culturels France-Italie </li>
              <li>Diplmes d'universit :   DLCI  et DLCR</li>
            </ul>
        },
        {
            name: "Universit Paris IV-Sorbonne",
            logo: require('../img/logo_UNI_ParisSorbonne.png'),
            website: "https://www.exemple2.com",
            courses: 
            <ul>
              <li>Deux parcours de spcialit (1. Littrature et culture ; 2. Langue et Traduction)</li>
              <li>Quatre bi-licences (Italien-Histoire ; Italien-Lettres modernes ; Italien-Lettres modernes appliques ; Italien-Musicologie)</li>
              <li>Master recherche en tudes italiennes (Littrature, Culture, Arts du spectacle et Traduction) </li>
              <li>Master recherche international en tudes italiennes en co-diplomation avec lUniversit La Sapienza de Rome</li>
            </ul>
        },
        // Ajoute d'autres universits si ncessaire
    ];
      const universitiesDataCreteil = [
        {
            name: " I.U.T. de Saint Denis - Campus de La Plaine",
            logo: require('../img/logo_IUT_StDenis.png'),
            website: "www.iutsd.univ-paris13.fr",
            courses: 
            <ul>
              <li>Cursus Techniques de commercialisation</li>
            </ul>
        },
        {
            name: "Paris Est Crteil Val de Marne (Paris XII)",
            logo: require('../img/logo_UNI_Upec.png'),
            website: "http://www.u-pec.fr/",
            courses: 
            <ul>
              <li>LEA : Langues trangres Appliques</li>
            </ul>
        },
        {
            name: "Universit Vincennes-Saint-Denis (Paris VIII)",
            logo: require('../img/logo_UNI_Paris8.png'),
            website: "https://univ-paris8.fr",
            courses: 
            <ul>
              <li>UFR langues, littratures, civilisations trangres - langues trangres appliques</li>
              <li>(UFR LLCE italien -LEA anglais -italien, allemand-italien, espagnol-italien)</li>
            </ul>
        },
        // Ajoute d'autres universits si ncessaire
    ];
      const universitiesDataVersailles = [
        {
            name: "Universit Paris X Nanterre",
            logo: require('../img/logo_UNI_Nanterre.png'),
            website: "https://dep-italien.parisnanterre.fr",
            courses: 
            <ul>
              <li>LLCER  (Licence mention langues, littratures et civilisations trangres et rgionales)
                <ul>
                  <li>Double licence (dlivrance de deux diplmes franais) en tudes italiennes et Etudes anglo-amricaines ou en tudes italiennes et Gographie et amnagement</li>
                  <li>Licence en double diplme (dlivrance d'un diplme franais et d'un diplme italien)</li>
                  <li>Licence en tudes italiennes</li>
                  <li>Licence en "tudes Europennes et Internationales" (LEEI)</li>
                  <li>Master tudes Romanes-Italien</li>
                  <li>Master Parcours International</li>
                  <li>Master Parcours International en Double diplme</li>
                </ul>
              </li>
            <li>Licence et Master Droit franais  Droit italien</li>
            <li> D.U. (Diplme d'Universit) Italien: Culture et communication professionnelle </li>
            <li> LEA (Langues trangres appliques) : Anglais-Italien</li>
            <li>Prparation au CELI (Certificat de connaissance de la langue italienne)</li>
            <li>Doctorat en tudes italiennes</li>
            </ul>
        },
        {
            name: " Universit de Versailles / Saint Quentin",
            logo: require('../img/logo_UNI_StQuentin.png'),
            website: "http://www.uvsq.fr/",
            courses: 
            <ul>
              <li>Licence Anglais / Droit</li>
              <li>Licence LLCE Anglais</li>
              <li>Licence LLCE Espagnol</li>
              <li>Licence de Lettres modernes</li>
              <li>Licence de Gographie</li>
            </ul>
        },
        {
            name: " Universit dEvry Val dEssonne",
            logo: require('../img/logo_UNI_Evry.png'),
            website: "http://www.univ-evry.fr/",
            courses: 
            <ul>
              <li>LEA : Langues trangres appliques (jusquau Master) en tant que langue complmentaire (LV3)</li>
              <li>Licence dHistoire (en tant que LV1 ou enseignement complmentaire)</li>
            </ul>
        },
        // Ajoute d'autres universits si ncessaire
    ];

    return (
        <div className="apprendre-container">
            <Hero
                title={title}
                description={description}
                heroImage= {heroImage}
                buttonText={buttonText}
            />
            <h2 className='h2-titre'>Apprendre l'italien aprs le BAC</h2>
            <h3 className="h3-Sections">Acadmie de Paris universits</h3>
            {universitiesDataParis.map((university, index) => (
                <UniversityTable key={index} university={university} />
            ))}
            <h3 className="h3-Sections">Acadmie de Crteil universits</h3>
            {universitiesDataCreteil.map((university, index) => (
                <UniversityTable key={index} university={university} />
            ))}
            <h3 className="h3-Sections">Acadmie de Versailles universits</h3>
            {universitiesDataVersailles.map((university, index) => (
                <UniversityTable key={index} university={university} />
            ))}
        </div>
    );
}

export default Apprendre;