import React from 'react'
import Hero from './Hero'
import heroImage from '../img/img-venise.jpg';
import './Enseigner.css';

const Enseigner = () => {

    const title = "Enseigner grce  notre association";
    const description = "Bienvenue sur notre page ddier  l'enseignement. Dcouvrez les avantages de devenir membre de notre association.";
    const buttonText = "Commencer";

    const handleScrollToContent = () => {}
        // Fonction pour faire dfiler la page vers la partie suivante

    const handleScrollToSection = (event, sectionId) => {
        event.preventDefault();
        const section = document.getElementById(sectionId);
        if (section) {
            section.scrollIntoView({ behavior: 'smooth' });
        }
    };

  return (
    <div className="enseigner-container">
            <Hero
                title={title}
                description={description}
                buttonText={buttonText}
                heroImage= {heroImage}
                scrollTo={handleScrollToContent}
            />
        <div className="enseigner-content1">
            <h1 className='h1'>Comment devenir enseignant</h1>
          <div className="content-txt">
            <p className='p'><span className='span'>La premire tape</span>, pour devenir enseignant, est avoir une licence (Bac+3).  Ds la licence, des parcours ou options de prparation aux mtiers de l'enseignement sont proposs aux tudiants qui dsirent s'engager dans cette voie.</p>
            <p className='p'><span className='span'>La deuxime tape</span> est de vous inscrire en master MEEF (mtiers de lenseignement, de lducation et de la formation), qui comporte plusieurs mentions : premier degr (professeur des coles), second degr (enseignant du secondaire), encadrement ducatif (conseiller principal dducation). Les diffrents concours auxquels vous aurez accs permettent denseigner dans le premier degr (coles maternelles et primaires) ou dans le secondaire (collges et lyces), dans le public ou dans le priv.</p>
            <p className='p'>La premire anne du master MEEF est surtout axe sur la prparation du concours de recrutement, qui a lieu  la fin de lanne, mais il faudra valider galement la deuxime anne car lobtention du diplme est indispensable pour exercer le mtier.</p>
            <p className='p'>Ces masters sont organiss par les ESPE (coles suprieures du professorat et de lducation) au sein des universits.</p>
            <p className='p'> noter que les universits et instituts catholiques proposent aussi des masters MEEF, tout indiqus si vous envisagez une carrire de professeur dans le priv.</p>
            <p className='p'>Au programme de tous ces concours : deux preuves crites dadmissibilit et deux preuves orales (une mise en situation professionnelle et un entretien).</p>
            <p className='p'>Si tout se passe bien (vous tes admis au concours et vous avez dcroch votre M1), vous poursuivez vos tudes en seconde anne de MEEF (si vous chouez au M1 mais pas au concours, vous gardez le bnfice du concours une anne).</p>
            <p className='p'>En cas dchec au concours, vous avez la possibilit de le repasser ou vous pouvez envisager une rorientation dans un autre master (au niveau M1 ou M2 selon que vous avez russi ou non vos examens).</p>
            <p className='p'>La seconde anne du master MEEF est considre comme une anne de formation en alternance : les tudiants deviennent fonctionnaires stagiaires et suivent des cours  lESPE tout en effectuant un stage en responsabilit quivalant  un mi-temps devant les lves. Durant cette priode, les enseignants stagiaires sont rmunrs entre 1.600  et 1.700  brut par mois.</p>
            
            <h3>En savoir plus : <a className="link" href='http://www.devenirenseignant.gouv.fr/pid33985/enseigner-au-college-ou-au-lycee-general-le-capes.html' target='_blank'>http://www.devenirenseignant.gouv.fr/pid33985/enseigner-au-college-ou-au-lycee-general-le-capes.html</a></h3>
          </div>

        </div>
    </div>
  )
}

export default Enseigner
